body {
  background: linear-gradient(0deg, #000, #5f9ea0);
  margin: 0;
  font-family: "Nunito", sans-serif;
  overflow: hidden;
}

.navbar {
  background: #000;
  width: 100%;
  height: 84px;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.sync {
  background: #fff;
  color: #fff;
  height: 30px;
  font-size: 14px;
  border-radius: 8px;
  border: 0px;
  margin-right: 15px;
  cursor: pointer;
  padding: 3px 8px 3px 8px;
}

.send,
.delete {
  background: #5f9ea0;
  color: #fff;
  transition-duration: 0.4s;
  height: 30px;
  font-size: 14px;
  border-radius: 8px;
  border: 0px;
  margin-right: 15px;
  cursor: pointer;
  padding: 3px 8px 3px 8px;
}

input {
  height: 30px;
  font-size: 14px;
  border-radius: 8px;
  border: 0px;
  margin-right: 15px;
  padding-left: 5px;
}

.send:hover,
.delete:hover {
  background: #fff;
  box-shadow: 0 1px 1px 0 #fff, 0 1px 2px 0 #fff;
  color: red;
}

.App {
  margin-top: 84px;
}

.liste {
  padding-top: 28px;
  height: 380px;
  padding-left: 48px;
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  overflow-y: hidden;
}

textarea {
  resize: none;
}

.scroll::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

.scroll::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}

.scroll::-webkit-scrollbar-thumb {
  background-color: #000;
}

.vote {
  padding: 5px;
  z-index: 10;
  text-align: center;
  position: relative;
  bottom: 0;
}
